        <input v-model="championName" placeholder="Enter a champion name" @keyup.enter="updateImage" />


        <div v-if="imageUrl" class="wrapper">
            <!-- Display the image of the champion -->
            <div class="lookup">
                <div class="image-container-lookup">
                    <img :src="imageUrl" :alt="championName" />
                </div>
            </div>
        </div>

        <div v-else class="no-image">
            <p>No image found. Please enter a valid champion name.</p>
        </div>
    </div>

