<style scoped>
.container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    /* 2 columns */
    gap: 20px;
    /* Space between boxes */
    max-width: 1200px;
    min-height: 300px;
    margin: auto;
    padding: 20px;
    justify-items: center;
}

.box {
    background-color: green;
    color: white;
    padding: 20px;
    text-align: center;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    justify-self: center;
    align-self: center;
}

.image-container {
    width: 100%;
    height: 100%;
    overflow: hidden;
    /* Hide parts that don't fit */
    position: relative;
    margin: 10px;

}

.image-container-lookup {
    width: 100%;
    height: 100%;
    overflow: hidden;
    /* Hide parts that don't fit */
    position: relative;
    margin-top: 30px;
}

.image-container img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    /* Ensures aspect ratio is maintained */
}

.image-container-lookup img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    /* Ensures aspect ratio is maintained */
}

.favorite {
    max-width: 1000px;
}

.mainDiv {
    display: flex;
    justify-content: center;
    align-items: center;
}

.lookup {
    max-width: 980px;
}


