<template>
  <div id="app">
    <h1>Cat Facts and Breeds</h1>

    <!-- Input fields for the user -->
    <div>
      <label for="facts-number">Number of Cat Facts:</label>
      <input type="number" v-model="factsCount" id="facts-number" />
      <button @click="getCatFacts">Get Cat Facts</button>
    </div>

    <!-- Displaying the cat facts -->
    <div v-if="catFacts.length">
      <h2>Cat Facts</h2>
      <ul>
        <!-- Ensure that each cat fact text is displayed correctly -->
        <li v-for="(fact, index) in catFacts" :key="index">{{ fact.text }}</li>
      </ul>
    </div>

    <!-- Input fields for cat breeds -->
    <div>
      <label for="breeds-number">Number of Cat Breeds:</label>
      <input type="number" v-model="breedsCount" id="breeds-number" />
      <button @click="getCatBreeds">Get Cat Breeds</button>
    </div>

    <!-- Displaying the cat breeds -->
    <div v-if="catBreeds.length">
      <h2>Cat Breeds</h2>
      <ul>
        <li v-for="(breed, index) in catBreeds" :key="index">{{ breed }}</li>
      </ul>
    </div>

    <!-- Input for searching cat breed images -->
    <div>
      <label for="breed-search">Enter Cat Breed or Search Term:</label>
      <input type="text" v-model="breedSearchQuery" id="breed-search" />
      <button @click="getCatBreedImages">Search Cat Breed Images</button>
    </div>

    <!-- Displaying the cat breed images -->
    <div v-if="catBreedImages.length">
      <h2>Cat Breed Images</h2>
      <div class="images">
        <img v-for="(image, index) in catBreedImages" :key="index" :src="image.urls.small" alt="Cat breed image" />
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      factsCount: 1,
      breedsCount: 1,
      catFacts: [],
      catBreeds: [], // Initialized as an empty array
      breedSearchQuery: '',
      catBreedImages: [],
    };
  },
  methods: {
    async getCatFacts() {
      try {
        const response = await axios.get(`https://catfact.ninja/facts?limit=${this.factsCount}`);

        // Check if response contains valid data
        if (response.data && Array.isArray(response.data.data)) {
          this.catFacts = response.data.data;
        } else {
          console.error('Unexpected response structure:', response);
        }
      } catch (error) {
        console.error('Error fetching cat facts:', error);
      }
    },

    async getCatBreeds() {
      try {
        const response = await axios.get('https://api.thecatapi.com/v1/breeds');
        if (response.data && Array.isArray(response.data)) {
          const allBreeds = response.data;

          // Shuffle the breeds array to get random breeds
          const shuffledBreeds = allBreeds.sort(() => Math.random() - 0.5);

          // Get the number of breeds the user wants
          this.catBreeds = shuffledBreeds.slice(0, this.breedsCount).map(breed => breed.name);
        } else {
          console.error('Unexpected response structure:', response);
        }
      } catch (error) {
        console.error('Error fetching cat breeds:', error);
      }
    },

    async getCatBreedImages() {
      const searchQuery = this.breedSearchQuery.trim();
      if (!searchQuery) {
        alert('Please enter a search term.');
        return;
      }

      try {
        const response = await axios.get(`https://api.unsplash.com/photos?query=${searchQuery}&client_id=YOUR_UNSPLASH_API_KEY`);
        this.catBreedImages = response.data;
      } catch (error) {
        console.error('Error fetching cat breed images:', error);
      }
    },
  },
};
</script>

<style>
/* Basic styling for images */
.images img {
  width: 200px;
  height: auto;
  margin: 10px;
}

/* Basic styling for facts and images */
ul {
  padding-left: 20px;
}

li {
  margin-bottom: 10px;
  font-size: 16px;
}
</style>