<template>
  <div class="container">
    <!-- Cat Facts Section -->
    <div class="cat-facts-container">
      <h1 class="title">Cat Facts</h1>

      <!-- Input Section for Cat Facts -->
      <div class="input-container">
        <label for="numFacts" class="input-label">Enter the number of cat facts you would like:</label>
        <input type="number" id="numFacts" v-model="numFacts" min="1" max="100" class="input-field"
          placeholder="Enter a number" />
        <button @click="getCatFacts" class="get-facts-btn">Get Facts</button>
      </div>

      <!-- Display Cat Facts -->
      <div v-if="catFacts.length > 0" class="facts-container">
        <ul class="fact-list">
          <li v-for="(fact, index) in catFacts" :key="index" class="fact-item">{{ fact }}</li>
        </ul>
      </div>
    </div>

    <!-- Cat Breeds Section -->
    <div class="cat-breeds-container">
      <h1 class="title">Cat Breeds</h1>

      <!-- Input Section for Cat Breeds -->
      <div class="input-container">
        <label for="numBreeds" class="input-label">Enter the number of cat breeds you would like:</label>
        <input type="number" id="numBreeds" v-model="numBreeds" min="1" max="100" class="input-field"
          placeholder="Enter a number" />
        <button @click="getCatBreeds" class="get-breeds-btn">Get Breeds</button>
      </div>

      <!-- Display Cat Breeds -->
      <div v-if="catBreeds.length > 0" class="breeds-container">
        <ul class="breed-list">
          <li v-for="(breed, index) in catBreeds" :key="index" class="breed-item">
            {{ breed.name }} - {{ breed.origin }}
          </li>
        </ul>
      </div>
    </div>
  </div>

  <body class="page2">
    <!-- this is for the animated background, not my code-->
    <!-- https://www.sliderrevolution.com/resources/css-animated-background -->
    <div class="bg1"></div>
    <div class="bg1 bg2"></div>
    <div class="bg1 bg3"></div>
    <!-- this is for the animated background, not my code-->
  </body>

</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      numFacts: 1, // Default number of facts
      catFacts: [], // Array to hold cat facts
      numBreeds: 1, // Default number of breeds
      catBreeds: [], // Array to hold cat breeds
    };
  },
  methods: {
    // Fetch Cat Facts
    async getCatFacts() {
      this.catFacts = []; // Reset the cat facts array before fetching new ones
      try {
        // Loop to fetch multiple random cat facts
        for (let i = 0; i < this.numFacts; i++) {
          const response = await axios.get('https://catfact.ninja/fact');
          this.catFacts.push(response.data.fact);
        }
      } catch (error) {
        console.error("Error fetching cat facts:", error);
      }
    },

    // Fetch Cat Breeds
    async getCatBreeds() {
      this.catBreeds = []; // Reset the cat breeds array before fetching new ones
      try {
        // Fetch the list of all cat breeds
        const response = await axios.get('https://api.thecatapi.com/v1/breeds');
        const allBreeds = response.data;

        // Randomly select the specified number of breeds
        for (let i = 0; i < this.numBreeds; i++) {
          const randomBreed = allBreeds[Math.floor(Math.random() * allBreeds.length)];
          this.catBreeds.push(randomBreed);
        }
      } catch (error) {
        console.error("Error fetching cat breeds:", error);
      }
    },
  },
};
</script>

<style scoped>
