<!-- this is the games page -->
<template>
  <div class="about">
    <h1>Tic-Tac-Toe</h1>
  </div>

  <div id="app">
    <div class="board">
      <div 
        v-for="(cell, index) in board" 
        :key="index" 
        class="cell"
        @click="makeMove(index)"
      >
        <span :class="{'x-piece': cell === 'X', 'o-piece': cell === 'O'}">
          {{ cell }}
        </span>
      </div>
    </div>
    <p v-if="winner" class="status">{{ winner }} wins!</p>
    <p v-else-if="isBoardFull()" class="status">It's a draw!</p>
    <button @click="resetGame" class="reset-btn">Reset Game</button>
  </div>

  <body class="page2">
  <!-- this is for the animated background, not my code-->
  <!-- https://www.sliderrevolution.com/resources/css-animated-background -->
  <div class="bg1"></div>
  <div class="bg1 bg2"></div>
  <div class="bg1 bg3"></div>
  <!-- this is for the animated background, not my code-->
   </body>

</template>


<script>
export default {
  data() {
    return {
      board: Array(9).fill(null), // 3x3 board as a flat array
      currentPlayer: "X", // Start with X
      winner: null, // No winner initially
    };
  },
  methods: {
    makeMove(index) {
      if (!this.board[index] && !this.winner) { // Prevent moves if square taken or game over
        this.board[index] = this.currentPlayer; // Make the move
        if (this.checkWin(this.currentPlayer)) {
          this.winner = this.currentPlayer; // Set the winner
        } else {
          this.currentPlayer = this.currentPlayer === "X" ? "O" : "X"; // Switch player
        }
      }
    },
    checkWin(player) {
      // Winning combinations
      const winningCombinations = [
        [0, 1, 2], [3, 4, 5], [6, 7, 8], // Rows
        [0, 3, 6], [1, 4, 7], [2, 5, 8], // Columns
        [0, 4, 8], [2, 4, 6],            // Diagonals
      ];
      // Check if any winning combination is satisfied
      return winningCombinations.some(combination => 
        combination.every(index => this.board[index] === player)
      );
    },
    isBoardFull() {
      return this.board.every(cell => cell !== null); // Check if all cells are filled
    },
    resetGame() {
      this.board = Array(9).fill(null); // Clear the board
      this.currentPlayer = "X"; // Reset to X's turn
      this.winner = null; // Clear the winner
    },
  },
};
</script>




