<!-- this is the secret rainbow cat page -->
<template>
    <!-- this is for the animated background, not my code-->
    <!-- https://www.sliderrevolution.com/resources/css-animated-background -->
    <body class="page2 sendDiagonalBack">
        <div class="bg1"></div>
        <div class="bg1 bg2"></div>
        <div class="bg1 bg3"></div>
        <!-- this is for the animated background, not my code-->
    </body>

    <!-- rainbow drop effect -->
    <!-- https://codepen.io/natewiley/pen/PwWxRd -->
    <div class="wrap sendRainBack">
        <div v-for="n in 144" :key="n" class="c"></div>
    </div>

    <!-- the cat gifs -->
    <div>
        <img src="rainbowCat.gif" alt="">
        <img src="rainbowCat.gif" alt="">
    </div>
    <div>
        <img src="rainbowCat.gif" alt="">
        <img src="rainbowCat.gif" alt="">
    </div>
</template>

<script>
export default {
    name: "DivWrapper",
};
</script>

<style scoped lang="scss">
$total: 144;
$time: 4;
$height: 400;
$percent: .69444%;
$dotSize: .9;

* {
    box-sizing: border-box;
}

html,
body {
    height: 100%;
}

body {
    background: black;
    overflow: hidden;
}

.wrap {
    position: relative;
    height: 100%;
}

.c {
    position: relative;
    height: $height+px;
    width: $percent;
    margin-top: -$height+px;
    animation: drop $time+s infinite ease-in;

    &:after {
        content: "";
        position: absolute;
        width: $dotSize+vw;
        height: $dotSize+vw;
        border-radius: 50%;
        left: 50%;
        bottom: -($dotSize/2)+vw;
        margin-left: -($dotSize/2)+vw;
    }
}

@for $i from 1 through $total {
    $hue: (
        300/$total) * $i;

    .c:nth-child(#{$i}) {
        left: ($i - 1) * $percent;
        background-image: linear-gradient(to bottom, black, hsla($hue, 100%, 50%, .8)
        );
    animation-delay: random($total) * ($time/$total) * -1s;

    &:after {
        background: hsla($hue, 100%, 50%, 1);
    }
}
}

@keyframes drop {
    80% {
        opacity: 1;
    }

    100% {
        transform: translate3d(0, 150vh, 0);
        opacity: 0;
    }
}

.sendRainBack {
    z-index: -1;
}
.sendDiagonalBack {
    z-index: -10;
}
</style>